@import "@/scss/settings.scss";
































































































.iziToast.toast_big.iziToast-layout2  {
	min-height: 150px;
	display: inline-flex;
	align-items: center;

	.iziToast-body  {
		.iziToast {
			&-title {
				font-size: 20px;
				margin-bottom: 10px;
			}

			&-message {
				font-size: 16px;
				margin-top: 5px;
				margin-bottom: 20px;
				strong {
					font-weight: 600;
				}
			}
		}
	}

	.iziToast {
		&-buttons-child {
			font-size: 16px;
			padding: 10px 15px;
			margin-top: 10px;
		}

		&-close {
			background-size: 15px;
		}
	}

}
