@import "@/scss/settings.scss";





















































.invoices {
	&__actions {
		margin-bottom: em(4);
	}

	&__list {
		.container {
			margin-bottom: em(2);
		}

		&-title {
			@include media($width_xs) {
				display: none;
			}
		}

		&-btns {
			display: flex;
			align-items: center;

			.app-btn__icon{
				padding: em(1);
				box-sizing: border-box;

				.app-icon {
					margin-left: 0;
				}

				&:not(:last-child) {
					margin-right: em(2);
				}
			}
		}
	}

	h4 {
		color: clr(dark, light);
		margin-top: 0;
	}

	.table {

		caption {
			@include media($width_xs) {
				display: block;
			}
		}
		&__td {
			@include media($width_md, 1) {
				min-width: 150px;

				&:nth-child(1) {
					min-width: 100px;
				}

				&:nth-child(3) {
					min-width: 100px;
				}
			}
		}

		&__thead {
			@include media($width_xs) {
				display: none;
			}
		}

		&__tbody {
			.table {
				&__td {
					color: clr(text, light);

					@include media($width_xs) {

						&:not(:first-child) {
							padding-top: 0;
						}

						&:before {
							content: attr(title);
							display: block;
							color: clr(dark, light);
							margin-bottom: 5px;
						}
					}
				}

				&__tr {
					@include media($width_xs) {
						display: flex;
						flex-direction: column;
						border-bottom: 1px solid clr(secondary, dark);
					}
				}
			}
		}
	}
}
