@import "@/scss/settings.scss";








































































































































































.invoices-manage {
	&__row {
		width: 100%;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		margin-bottom: em(3);

		&:last-child {
			margin-bottom: 0;
		}
	}

	&__btn {
		margin-top: em(3);
		display: flex;
		align-items: center;
		justify-content: space-between;

		.app-btn {
			&:first-child {
				padding: em(1);

				.app-icon {
					margin-left: 0;
				}
			}
		}
	}

	&__fieldset {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		.app-field__wrapper {
			width: calc(50% - 10px);

			@include media($width_xs) {
				width: 100%;

				&:not(:last-child) {
					margin-bottom: em(3);
				}
			}

			.app-icon {
				pointer-events: none;
			}

			&.field {
				&__textarea {
					width: 100%;
					max-width: 100%;

					textarea {
						resize: vertical;
						min-height: 200px;
						max-height: 400px;
					}
				}
			}
		}
	}
}
