html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%; }

body {
  margin: 0; }

h1 {
  font-size: 2em;
  margin: .67em 0; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

pre {
  font-family: monospace, monospace;
  font-size: 1em; }

a {
  background-color: transparent; }

abbr[title] {
  border-bottom: 0;
  text-decoration: underline;
  text-decoration: underline dotted; }

b,
strong {
  font-weight: bolder; }

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sub {
  bottom: -0.25em; }

sup {
  top: -0.5em; }

img {
  border-style: none; }

button,
input,
optgroup,
select {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0; }

textarea {
  margin-bottom: 0;
  font-weight: normal;
  font: inherit; }

button,
input {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button; }

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0; }

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText; }

fieldset {
  padding: .35em .75em .625em; }

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal; }

progress {
  vertical-align: baseline; }

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto; }

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px; }

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit; }

details {
  display: block; }

summary {
  display: list-item; }

template {
  display: none; }

[hidden] {
  display: none; }

.g-grid {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px; }
  .g-grid_lg {
    max-width: 1600px; }

.g-title__l {
  position: relative;
  padding-left: 30px;
  padding-bottom: 0.625em;
  padding-top: 0.625em;
  margin: 1.25em 0; }
  .g-title__l-line {
    background-color: #F1F3F6;
    position: relative;
    z-index: 1;
    padding-right: 20px; }
  .g-title__l:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 7px;
    height: auto;
    display: flex;
    background-color: #E4E6E9; }
  .g-title__l:after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 30px;
    right: 0;
    margin: auto;
    height: 2px;
    background-color: #E4E6E9;
    display: block;
    width: auto; }

.g-fieldset {
  margin: 0;
  padding: 1.875em 1.25em;
  border: 1px solid #E4E6E9; }

body {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F1F3F6;
  color: #7A8398;
  font-size: 16px;
  line-height: 1.2; }

a {
  color: #4973FF;
  text-decoration: none;
  transition: all 0.3s ease-in-out; }
  a:hover {
    color: #6387ff; }

fieldset {
  margin: 0;
  padding: 1.25em 1.25em;
  border: 1px solid #E4E6E9; }

.container {
  padding: 1.25em 1.25em;
  background-color: #fff; }

.table {
  border-collapse: collapse;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  overflow: hidden;
  width: 100%; }
  .table__td {
    padding: 1.25em; }
  .table__thead,
  .table caption {
    background-color: #F1F3F6; }
  .table caption {
    padding: 1.25em;
    text-align: left;
    font-weight: 600;
    display: none; }
  .table__tbody {
    background-color: #fff; }
    .table__tbody .table__tr {
      border-bottom: 1px solid #F1F3F6; }
