@import "@/scss/settings.scss";
































































































































































    .app-field {
		$af: &;
		$field: '.field';

		&__input:not(.vc-reset),
		&__input.vc-reset input {
            min-height: 20px;
            padding: em(1) em(2);
            outline: none;
            border: 1px solid clr(text, base);
            transition: all $trans;
            width: 100%;
            box-sizing: border-box;
			border-radius: $br_1;
			box-shadow: none;

            &:read-only {
                cursor: pointer;
            }

            &:focus{
				border-color: clr(primary);
				box-shadow: none;
            }
        }

        &__error {
            font-size: 12px;
            color: clr(error);
            font-weight: normal;
            position: absolute;
			margin-top: 2px;
			display: none;
        }

        &__label {
            margin-bottom: 5px;
            color: clr(dark, light);
            font-weight: 600;
            font-size: $fsz_h6;
        }

        &__inner {
            position: relative;
            width: 100%;
        }

        &__wrapper {
            width: 50%;
            max-width: 600px;
			position: relative;

			&:not([aria-hide-error="true"]) {
				#{$af}__error {
					display: block;
				}
			}

            &#{$field} {
                &__error:not([aria-hide-error="true"]){
                    #{$af} {
						&__input,
						&__input.vc-reset input {
                            border-color: clr(error);
                        }
                    }

                    &#{$field}__icon {
                        .app-icon {
                            border-color: clr(error);
                            fill: clr(error);
                        }
                    }
                }

                &__icon {
                    .app-icon {
                        cursor: pointer;
                        width: 20px;
                        height: 100%;
                        position: absolute;
                        right: 0;
                        top: 0;
                        bottom: 0;
                        margin: auto;
                        padding: 0 20px;
                        border-left: 1px solid clr(text, base);
                        transition: all $trans;
                        fill: clr(text, base);
                    }
				}

				&__disabled {
					opacity: .55;
					cursor: default;

					#{$af}__input {
						cursor: auto;
					}
				}
            }

            &:focus,
			&:focus-within {
                #{$af} {
					&__input,
					&__input.vc-reset input {
                        border-color: clr(primary)
                    }
                }
			}

			&#{$field}__has_value[aria-hide-error] {
				#{$af} {
					&__input,
					&__input.vc-reset input {
                        border-color: clr(success)
                    }
                }

                &#{$field}__icon {
                    .app-icon {
                        border-color: clr(success);
                        fill: clr(success);
                    }
                }
			}
        }
    }

