@import "@/scss/settings.scss";























































.app-btn {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	border: none;
	outline: none;
	background-color: clr(primary);
	color: clr(white);
	padding: em(1) em(4);
	border-radius: $br_1;
	cursor: pointer;
	transition: all $trans;

	&:hover {
		background-color: clr(primary, light);
		color: clr(white);
	}

	&__disabled {
		background-color: clr(secondary, dark);

		&:hover {
			background: clr(secondary, dark_h);
		}
	}

	&__icon {
		.app-icon {
			width: 20px;
			height: 20px;
			fill: clr(white);
			margin-left: 5px;
		}
	}
}
